import { computed, defineComponent, ref } from 'vue';
import ClientQrCodeComp from '@/components/client-qr-code/index.vue';
import UserInfoComp from '@/components/user-info/index.vue';
import useHttpRequest from '@/utils/request';
import { useStore } from 'vuex';
import { ElEmpty, ElMessage, ElSkeleton } from 'element-plus';
import { formatDate } from '@/utils';
import mitt from 'mitt';
export var emitter = mitt();
export default defineComponent({
    components: {
        ElSkeleton: ElSkeleton,
        ElEmpty: ElEmpty,
        ClientQrCodeComp: ClientQrCodeComp,
        UserInfoComp: UserInfoComp
    },
    setup: function () {
        var state = useStore().state;
        // 登录标识
        var token = computed(function () {
            return state.user.token;
        });
        // 列表数据获取
        var _a = useHttpRequest(), isLoading = _a.isLoading, adornUrl = _a.adornUrl, httpRequest = _a.httpRequest;
        var message = ref();
        var getData = function () {
            if (token.value) {
                httpRequest({
                    url: adornUrl("/api/v1/messages"),
                    method: 'get',
                    params: {
                        accesstoken: token.value,
                        mdrender: true
                    }
                }).then(function (_a) {
                    var data = _a.data;
                    message.value = data.data;
                }).catch(function (e) {
                    ElMessage.error('请求失败');
                    console.error(e);
                });
            }
        };
        getData();
        // 标记消息已读
        var readHttpRequest = useHttpRequest().httpRequest;
        var readAll = function () {
            readHttpRequest({
                url: adornUrl("/api/v1/message/mark_all"),
                method: 'post',
                params: {
                    accesstoken: token.value
                }
            }).then(function () {
                emitter.emit('read-msg');
                getData();
            }).catch(function (e) {
                ElMessage.error('请求失败');
                console.error(e);
            });
        };
        var readOne = function (id) {
            readHttpRequest({
                url: adornUrl("/api/v1/message/mark_one/" + id),
                method: 'post',
                params: {
                    accesstoken: token.value
                }
            }).then(function () {
                emitter.emit('read-msg');
                getData();
            }).catch(function (e) {
                ElMessage.error('请求失败');
                console.error(e);
            });
        };
        return {
            message: message,
            isLoading: isLoading,
            readAll: readAll,
            readOne: readOne,
            formatDate: formatDate,
        };
    },
});
